.svg {
  display: block;
  overflow: visible;
}

.activeLine {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.inactiveLine {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.label {
  font-family: var(--font-code);
  user-select: none;
}

/* Glow animation */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}
